.Header {
  display: flex;
  align-items: center;
  width: 90%;
  margin: 24px 67px 0;

  .logo {
    width: 63px;
    height: 63px;
    cursor: pointer;
    transition: .3s linear;

    &:hover {
      opacity: .7;
    }
  }

  .container {
    margin: 0 0 0 auto;
    display: flex;
  }
}
